import React, { useState, useEffect } from 'react';
import "../styles/AdminOrders.css";

const AdminOrders = () => {
    const [orders, setOrders] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        fetchOrders();
    }, []);

    const fetchOrders = async () => {
        try {
            const response = await fetch(`${process.env.REACT_APP_API_URL}api/orders`);
            if (!response.ok) {
                throw new Error('  ');
            }
            const data = await response.json();
            setOrders(Array.isArray(data) ? data : []);
        } catch (error) {
            console.error('   :', error);
            setError(error.message || '-   ');
        } finally {
            setLoading(false);
        }
    };

    const completeOrder = async (orderId) => {
        try {
            const response = await fetch(`${process.env.REACT_APP_API_URL}api/orders/${orderId}`, {
                method: 'DELETE',
            });
            if (!response.ok) {
                throw new Error('   ');
            }
            setOrders(orders.filter(order => order.id !== orderId));
            alert('   !');
        } catch (error) {
            console.error('   :', error);
            alert('   ');
        }
    };

    if (loading) return <div>...</div>;
    if (error) return <div>: {error}</div>;

    return (
        <div className="orders-container">
            {orders.map((order) => (
                <div key={order.id} className="order">
                    <h3> ID: {order.id}</h3>
                    <p className="total-price"> : {order.totalPrice}</p>
                    {order.address ? (
                        <div className="address">
                            <p> Email: <span>{order.user.email}</span></p>
                            <p>: <span>{order.address.address}</span></p>
                        </div>
                    ) : (
                        <p className="no-address">  </p>
                    )}
                    <div className="order-devices">
                        <h4>:</h4>
                        <ul>
                            {order.orderDevices?.map((orderDevice) => (
                                <li key={orderDevice.id}>{orderDevice.device.name}</li>
                            ))}
                        </ul>
                    </div>
                    <button onClick={() => completeOrder(order.id)} className="complete-order"> </button>
                </div>
            ))}
        </div>
    );
};

export default AdminOrders;


















