import '../styles/PaymentPage.css';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useBasket } from '../context/BasketContext';
import { SHOP_ROUTE } from '../utils/consts';
import axios from 'axios';
import '../styles/PaymentPage.css';

const PaymentPage = () => {
    const { basket, clearBasket } = useBasket();
    const [paymentStatus, setPaymentStatus] = useState(null);
    const [paymentMessage, setPaymentMessage] = useState("");
    const [address, setAddress] = useState("");
    const navigate = useNavigate();

    const totalPrice = basket.reduce((sum, item) => sum + item.price, 0);

    const handlePayment = async () => {
        if (!address) {
            setPaymentMessage(',   .');
            setPaymentStatus('error');
            return;
        }

        const isPaymentSuccessful = true;

        if (isPaymentSuccessful) {
            setPaymentMessage('  !');
            setPaymentStatus('success');
            clearBasket();

            try {

                if (basket.length === 0) {
                    throw new Error(' ');
                }

                const devices = basket.map(item => ({ deviceId: item.id }));

                console.log('Devices:', devices);
                console.log('Total Price:', totalPrice);

                const response = await axios.post(`${process.env.REACT_APP_API_URL}api/orders/create`, {
                    devices: devices.map(device => ({
                        deviceId: device.deviceId,
                        price: device.price || 0,
                    })),
                    totalPrice: totalPrice,
                    address: address,
                }, {
                    headers: {
                        Authorization: `Bearer ${localStorage.getItem('token')}`,
                    },
                });
                console.log("   :", address);

                console.log(' :', response.data);


                setPaymentMessage('    !');
                setPaymentStatus('success');

            } catch (error) {
                console.error("   :", error);
                setPaymentMessage(error.message || '   ');
                setPaymentStatus('error');
            }


            setTimeout(() => {
                navigate(SHOP_ROUTE);
            }, 200);

        } else {
            setPaymentMessage('  ');
            setPaymentStatus('error');
        }
    };

    return (
        <div className="payment-page">
            <h2> </h2>

            <div className="payment-items">
                <h3>  :</h3>
                <ul>
                    {basket.map((item) => (
                        <li key={item.id} className="payment-item">
                            {item.name} - {item.price} 
                        </li>
                    ))}
                </ul>
            </div>

            <div className="total-price">
                <h3>: <strong>{totalPrice} </strong></h3>
            </div>

            <div className="address-input">
                <label htmlFor="address">  :</label>
                <input
                    type="text"
                    id="address"
                    value={address}
                    onChange={(e) => setAddress(e.target.value)}
                    placeholder="  "
                />
            </div>

            <div className="payment-instruction">
                <p><strong>:</strong>       ,      .       .       . , ,          .   !</p>
            </div>

            {paymentStatus === 'success' && (
                <p className="success-message">{paymentMessage}</p>
            )}

            {paymentStatus === 'error' && (
                <p className="error-message">{paymentMessage}</p>
            )}

            <button onClick={handlePayment} className="payment-btn">
                
            </button>
        </div>
    );
};

export default PaymentPage;








